body, html {
  height: 100%;
}

#root {
  height: 100%;
}

.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  color: white;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: #303030;
}

.full {
  width: 100%;
  height: 100%;
}

.text {
  margin: 10px;
}

